<template>
  <section>
    <MsgFB/>
    <form class="card p20" @submit.prevent="toLogin" style="text-align: center">
      <div class="fb fz14" style="margin-bottom: 10px">
        <label for="email">Email</label>
        <input
          id="email"
          type="text"
          v-model.trim="loginForm.email"
          class="ml20 wInput"
        >
      </div>
      <div class="fb fz14">
        <label for="password">Password</label>
        <input
          id="password"
          type="password"
          v-model.trim="loginForm.password"
          class="ml20 wInput"
        >
      </div>
      <button style="margin-top: 10px" class="loginBtn" type="submit">Login</button>
    </form>
  </section>
</template>

<script>
  export default {
    data() {
      return {
        loginForm:{
          email:null,
          password:null
        },
        registerForm:{
          email:null,
          password:null,
          name:null
        },
      };
    },
    methods: {
      async toLogin(){
        try{
          await this.$store.dispatch('login',this.loginForm);
          await this.$router.push('/')
        }catch(error){}//вывод ошибок в сторе в общ шине а не здесь
      },
    },
  };
</script>


